import { createTheme } from "@mui/material";

export const theme = createTheme({
    palette: {
      inputcolor: {
        title: "#596BB3",
        text: "#333D66",
        desc: "black",
      },
      primary: {
        main: "#333D66",
        contrastText: "white",
      },
      secondary: {
        main: "#8F96B3",
        light: "#9EA4B3",
        contrastText: "white",
      },
    },
  });
  
  export const classes = {
    mainContainer: {
      padding: "2rem",
      display: "flex",
      alignItems:"center",
        minHeight: "100vh",
    },
    mainPaper: {
      boxShadow: "0 0 8px 1px rgba(62,65,79,0.09)",
      borderRadius: "6px",
      backgroundColor: "#FAFAFA",
    },
    Rside:{
      display:"flex",
      flexDirection:'column',
      justifyContent:"center",
      // padding:"6rem 2rem",
    },
    me: {
      width: "100%",
      borderRadius: "6px 0 0 0",
    },
    name: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
      fontSize: "2.6rem",
      color: theme.palette.inputcolor.text,
      lineHeight: "47px",
      fontWeight: "bold",
      textAlign: "center",
    },
    mr: {
      padding: theme.spacing(0, 2),
    },
    title: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
    //   opacity: 0.8,
      color: theme.palette.inputcolor.title,
      letterSpacing: "4px",
      fontWeight: "bold",
      textAlign: "center",
    },
    divider: { borderColor: theme.palette.inputcolor.text },
    dividerx: { borderColor: theme.palette.inputcolor.text, display:{ xs: 'block', md: 'none' } },
    desc: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      opacity: 0.8,
      lineHeight: "1rem",
      color: theme.palette.inputcolor.desc,
    },
    infoHeader: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
    //   opacity: 0.8,
      color: theme.palette.inputcolor.title,
      letterSpacing: "2px",
      fontWeight: "bold",
      marginBottom: theme.spacing(1),
    },
    contact: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
      fontSize: ".6rem",
      color: theme.palette.inputcolor.desc,
        borderColor: theme.palette.inputcolor.title,

      letterSpacing: "2px",
      fontWeight: "bolder",
      marginBottom: theme.spacing(1),
      cursor: "pointer",

    },
    link: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textDecoration: "none",
      color: theme.palette.inputcolor.desc,
      fontWeight: "500",
    },
    skills: {
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
      fontSize: "12px",
      color: theme.palette.primary.desc,
      textAlign: "center",
      fontWeight: "bolder",
      transition: "all 0.2s ease-in-out",
      cursor: "pointer",
      "&:hover": {
        color: theme.palette.inputcolor.text,
        transform: "perspective(500px) translateZ(200px)",
      },
    },
    stitle:{
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
      fontSize: "11px",
      color: theme.palette.inputcolor.text,
      letterSpacing: "2px",
      fontWeight: "bolder",
    },
    sname:{
      fontFamily: "'Yanone Kaffeesatz', sans-serif",
      textTransform: "uppercase",
      fontSize: "11px",
      color: theme.palette.inputcolor.desc,
      marginLeft: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    Ltitle:{
        fontFamily: "'Yanone Kaffeesatz', sans-serif",
        textTransform: "uppercase",
        fontSize: "1.4rem",
        color: theme.palette.inputcolor.text,
        letterSpacing: "2px",
        fontWeight: "bolder",
    },
    mainTitle:{
        fontFamily: "'Yanone Kaffeesatz', sans-serif",
        textTransform: "uppercase",
        fontSize: "2rem",
        color: theme.palette.inputcolor.title,
        letterSpacing: "2px",
        fontWeight: "bolder",
        textAlign:"right"
    },
    where:{
        fontFamily: "'Yanone Kaffeesatz', sans-serif",
        textTransform: "uppercase",
        fontSize: "1rem",
        color: theme.palette.secondary.main,
        letterSpacing: "2px",
        fontWeight: "500",
    },
    Ldesc: {
        fontFamily: "'Yanone Kaffeesatz', sans-serif",
        opacity: 0.8,
        lineHeight: "1rem",
        color: theme.palette.inputcolor.desc,
        paddingBottom:theme.spacing(2)
      },
  
  };