import {
  Container,
  Divider,
  Grid,
  Paper,
  Stack,
  Typography,
} from "@mui/material";
import { LeftsideContent } from "./Content/Leftside";
import { RightsideContent, RightsideContentP } from "./Content/Rightside";
import { classes } from "./style";
import { Box } from "@mui/system";
import AOS from "aos";
import { useEffect } from "react";
const MapLeftside = () => {
  return LeftsideContent.map((e, index) => (
    <Grid
      item
      xs={12}
      key={index}
      sx={!e.nopadding ? classes.mr : {}}
      data-aos="zoom-in"
    >
      {e.item}
    </Grid>
  ));
};

const MapRightside = ({ Data }) => {
  return Data.map((e, index) => (
    // <Grid item xs={12} key={index} sx={!e.nopadding ? classes.mr : {}}>
    // </Grid>
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="flex-start"
      spacing={1}
      key={index}
      data-aos="zoom-in-left"
    >
      <Typography sx={classes.Ltitle}>{e.title}</Typography>
      <Typography sx={classes.where}>{e.where}</Typography>
      <Typography
        sx={classes.Ldesc}
        // style={
        //   index + 1 === Data.length ? { paddingBottom: 0 } : {}
        // }
      >
        {e.desc}
      </Typography>
    </Stack>
  ));
};

function App() {
  useEffect(() => {
    AOS.init({ once: true});
  }, []);

  return (
    <>
      <Container maxWidth="md" sx={classes.mainContainer}>
        <Paper sx={classes.mainPaper}>
          <Grid container>
            <Grid container item xs={12} md={4} rowSpacing={2}>
              <MapLeftside />
            </Grid>
            <Divider orientation="vertical" flexItem />
            <Grid item xs={12} md sx={classes.Rside}>
              <Box padding="6rem 2rem" >
                <Typography
                  sx={classes.mainTitle}
                  data-aos="fade-zoom-in"
                  data-aos-easing="ease-in-back"
                  // data-aos-delay="300"
                  data-aos-offset="0"
                >
                  WORK EXPERIENCE
                </Typography>
                <MapRightside Data={RightsideContent} />
                <Typography
                  sx={classes.mainTitle}
                  data-aos="fade-zoom-in"
                  data-aos-easing="ease-in-back"
                  data-aos-delay="100"
                  data-aos-offset="0"
                >
                  PERSONAL EXPERIENCE
                </Typography>
                <MapRightside Data={RightsideContentP} />
              </Box>
            </Grid>
          </Grid>
        </Paper>
      </Container>
    </>
  );
}

export default App;
